*,
::after,
::before {
    box-sizing: border-box;
    scrollbar-width: thin;
    scrollbar-color: white #ccc;
}

body {
    margin: 0;
    font-family:
        Comic Sans MS,
        cursive;
    font-optical-sizing: none;
    font-weight: 400;
    font-size: 1.3rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 3px !important;

    scrollbar-color: transparent !important;
    /*scroll de baixo*/
    overflow-x: hidden !important;
    /*scroll do lado*/
    overflow-y: auto !important;
}
*::selection {
    color: black;
    background: #56ccf2;
}

code {
    font-family: source-code-pro, Menlo, Monaco,
        Consolas, 'Courier New', monospace;
}

.card-color-hover:focus{
    background: #fff0e2fa;
}
.card-color-hover:hover {
    background: #fff0e2fa;
}

.hero-input {
    font-family: cursive;
    font-size: 1rem;
    color: blue;
    letter-spacing: 2px;
    text-transform: uppercase;

    background: #fff;
    outline: none !important;
    border: none;

    width: 60%;
    transition:
        width 0.5s,
        background 0.2s;
}

.hero-input::placeholder {
    color: #434343;
    text-transform: capitalize;
}
.hero-input:focus {
    width: 90%;
    outline: thick double #f5f5f5 !important;
    text-transform: lowercase;
    background: #434343;
    color: #f5f5f5;
}

.hero-input:hover {
    width: 90%;
    outline: thick double #f5f5f5 !important;
    text-transform: lowercase;
    background: #434343;
    color: #f5f5f5;
    cursor: text;
}

.hero-input::selection {
    color: black;
    background: #56ccf2;
}

.hero-btn:focus {
    background: #434343 !important;
    color: #fff !important;
}

.hero-btn:hover {
    background: #434343 !important;
    color: #fff !important;
}

/*style table*/
.style-td {
    white-space: nowrap !important;
    text-align: center !important;
    padding: 1rem 0 1rem 0 !important;
}

/*media css*/
@media only screen and (max-width: 1024px) {
    .style-td {
        white-space: wrap !important;
        text-align: center;
    }
}

@media only screen and (max-width: 768px) {
    .style-font-size {
        text-transform: lowercase !important;
        text-align: left !important;
    }
    .style-td {
        white-space: wrap !important;
        text-align: center;
        padding: 1rem 1px 1rem 1px !important;
    }
}

@media only screen and (max-width: 430px) {
    .style-td {
        white-space: nowrap !important;
        text-align: center;
        padding: 1rem 1px 1rem 1px !important;
    }
}

@media only screen and (max-width: 425px) {
    .style-td {
        white-space: nowrap !important;
        text-align: center;
        padding: 1rem 9px 1rem 9px !important;
    }
}

@media only screen and (max-width: 375px) {
    .style-td {
        white-space: nowrap !important;
        text-align: center;
        padding: 1rem 9px 1rem 9px !important;
    }
}

@media only screen and (max-width: 320px) {
    .style-td {
        white-space: nowrap !important;
        text-align: center;
        padding: 1rem 9px 1rem 9px !important;
    }
}
